<template>
  <v-card class="rounded-card pa-2">
		<v-card-text>
			<v-layout row>
				<v-flex xs9>
					<h1>{{ teamInfo.group }}</h1>
					<h4 class="white--text">Third Place</h4>
				</v-flex>
				<v-flex>
					<h1 style="font-size:54px;color:#A8C256;">
						{{ teamInfo.total }}
					</h1>
				</v-flex>
			</v-layout>
		</v-card-text>
  </v-card>
</template>
<script>
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'Info',
  data () {
    return {
    }
  },
  computed: {
    ...mapState({
     currentTournament: state => state.tournament.currentTournament
    }),
    ...mapGetters({
      teamInfo: 'leaderboards/team/getTeamInfo'
    })
  },
	methods: {
    // scorecardPage () {
    //   this.$router.push(
    //     {
    //       name: 'Scorecard',
    //       params: {
    //         id: this.currentTournament.id,
    //         scorecard_id: this.roundId
    //       }
    //     }
    //   )
    // }
  },
  mounted: function () {}
}
</script>
<style scoped>
.team-text-color {
	color: #ACA885;
}
.team-background-color {
  background-color: #ACA885;
}
.rounded-card {
  background-color: grey;
	border-radius: 20px;
  transition: all 0ms cubic-bezier(0.645, 0.045, 0.355, 1);
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.1);
}
/* #9FB8CE */
</style>



